/**
 * Play Page - Game Selection and Play Interface
 */

'use client';

import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Play, Target, Music, Clock } from 'lucide-react';

interface GameMode {
  id: string;
  name: string;
  description: string;
  icon: React.ElementType;
  difficulty: 'Easy' | 'Medium' | 'Hard';
  color: string;
}

const gameModes: GameMode[] = [
  {
    id: 'osu',
    name: 'osu! Mode',
    description: 'Classic circle-tapping gameplay with approach circles',
    icon: Target,
    difficulty: 'Medium',
    color: 'from-blue-500 to-cyan-500'
  },
  {
    id: 'pin',
    name: 'Pin Mode',
    description: 'Knife Hit style gameplay - throw pins at the spinning target',
    icon: Target,
    difficulty: 'Easy',
    color: 'from-purple-500 to-pink-500'
  }
];

export default function PlayPage() {
  const [selectedMode, setSelectedMode] = useState<string | null>(null);
  const [selectedSong, setSelectedSong] = useState<any>(null);

  const handleModeSelect = (modeId: string) => {
    setSelectedMode(modeId);
  };

  const startGame = () => {
    if (selectedMode && selectedSong) {
      // Navigate to game with selected mode and song
      console.log(`Starting ${selectedMode} mode with song:`, selectedSong);
    }
  };

  return (
    <div className="min-h-full bg-gradient-to-br from-slate-900 via-purple-900/20 to-slate-900 p-6">
      <div className="max-w-7xl mx-auto">
        <div className="mb-8">
          <h1 className="text-4xl font-bold text-white mb-2">Choose Your Mode</h1>
          <p className="text-slate-400 text-lg">Select how you want to experience the rhythm</p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 mb-8">
          {gameModes.map((mode) => {
            const Icon = mode.icon;
            const isSelected = selectedMode === mode.id;
            
            return (
              <Card 
                key={mode.id}
                className={`cursor-pointer transition-all duration-300 hover:scale-105 ${
                  isSelected 
                    ? 'bg-slate-800 border-purple-500 shadow-lg shadow-purple-500/25' 
                    : 'bg-slate-800/50 border-slate-700 hover:border-slate-600'
                }`}
                onClick={() => handleModeSelect(mode.id)}
              >
                <CardHeader>
                  <div className="flex items-center gap-4">
                    <div className={`w-16 h-16 rounded-xl bg-gradient-to-br ${mode.color} flex items-center justify-center`}>
                      <Icon className="w-8 h-8 text-white" />
                    </div>
                    <div>
                      <CardTitle className="text-white text-xl">{mode.name}</CardTitle>
                      <div className="flex items-center gap-2 mt-1">
                        <span className={`px-2 py-1 rounded text-xs font-medium ${
                          mode.difficulty === 'Easy' ? 'bg-green-500/20 text-green-400' :
                          mode.difficulty === 'Medium' ? 'bg-yellow-500/20 text-yellow-400' :
                          'bg-red-500/20 text-red-400'
                        }`}>
                          {mode.difficulty}
                        </span>
                      </div>
                    </div>
                  </div>
                </CardHeader>
                <CardContent>
                  <CardDescription className="text-slate-300 text-base">
                    {mode.description}
                  </CardDescription>
                </CardContent>
              </Card>
            );
          })}
        </div>

        {selectedMode && (
          <Card className="bg-slate-800/50 border-slate-700">
            <CardHeader>
              <CardTitle className="text-white flex items-center gap-2">
                <Music className="w-5 h-5" />
                Select a Song
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-center py-12">
                <div className="text-slate-400 mb-4">
                  <Music className="w-16 h-16 mx-auto mb-4 opacity-50" />
                  No songs available yet
                </div>
                <p className="text-slate-500 mb-6">
                  Import .osz files from the Library section to start playing
                </p>
                <Button 
                  variant="outline" 
                  className="border-slate-600 text-slate-300 hover:bg-slate-700"
                >
                  Go to Library
                </Button>
              </div>
            </CardContent>
          </Card>
        )}
      </div>
    </div>
  );
}
